.geniusify{
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: center;
}

.geniusify__body{
    background-color: rgba(255, 255, 255, 0.15);
    width: 50%;
    margin-top: 2.5rem;
    padding: 0.7rem;
    border-radius: 2rem;
}

.geniusify h1{
    color: white;
    margin-top: 2rem;
    margin-bottom: 0;
    letter-spacing: 1.2vw;
    font-size: 3rem;
    font-weight: 800;
}

.geniusify-textarea__wrapper{
    margin: 3vh;
}

.geniusify-textarea{
    width: 100%;
    max-width: 600px;
    height: 10rem;
    text-align: left;
    box-sizing: border-box;
    resize: none;
    border-radius: 3px;
    border: none;
    outline: inherit;
    padding: 1rem;
}

.geniusify-textarea:focus{
    outline: none;
}

.geniusify-button{
    border: none;
    width: 8rem;
    height: 2.4rem;
    border-radius: 3px;
    color: #187bcd;
    font-weight: 700;
}

.geniusify-button:hover,:focus{

    outline: none;
}

.genuisfy-lowbutton_wrapper{
    margin-bottom: 3rem;
}

@media(max-width:1000px){
    .geniusify h1{
        margin-top: 1rem;
        letter-spacing: 1.3vw;
        font-size: 2rem;
    }    
    .geniusify-textarea__wrapper{
        margin: 1rem;
    }
    .geniusify__body{
        width: 90%;
    }
}